<template>
	<view class="Exchange">
		<view class="list-wrap" v-for="(item,index) in waitList" :key="index" @click="handlSongda(index)">
			<view class="item-left">
				<view class="item-user">
					<text class="user-name">{{item.orde.Name}}</text>
					<text class="user-phone">{{item.orde.Phone}}</text>
				</view>
			</view>
			<view class="item-right">
				<image src="../../static/shang.png" mode="widthFix"></image>
			</view>
		</view>
		<view class="title" v-if="Merchants">没有更多中奖啦!!!...</view>
	</view>
</template>

<script>
	import common from '../../common/common';
	var _self;
	export default {		
		data() {
			return {
				userinfo:'',
				waitList:[]	,
				page:1,
				total:0,
				Merchants:false
			}
		},
		onLoad(){
			_self = this;
			_self.userinfo = common.getstate()			
			_self.prizeQuest(1)
		},
		onReachBottom(){      // 页面触底事件
			_self.page += 1;   // 当前页数
			if(_self.page > _self.total){				
				_self.Merchants = true;				
				return
			}	
			_self.prizeQuest(_self.page);
		},
		methods: {						
			prizeQuest(page){
				common.request({
					path: "Order/getpage",
					data: {
						amoutype:'1',
						memerid: _self.userinfo.ID,
						rows: 10,
						page: page,
						sidx: "LssueTime",
						sord: "desc"						
					},
					success: res => {
						_self.waitList = [..._self.waitList,...res.data.rows];
						_self.total = res.data.total
					}
				})
			},
			handlSongda(i){
				uni.navigateTo({
					url: './prizeFinish?waitList=' + encodeURIComponent(JSON.stringify(_self.waitList[i])),
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.title{
		width: 100%;
		text-align: center;
		padding: 40rpx 0;
		color: #ccc;
	}
	.Exchange{
		width: 94%;
		margin: 0 auto;
		.list-wrap {
			margin-top: 30rpx;
			display: flex;
			background-color: #fff;
			justify-content: space-between;
			align-items: center;
			padding: 30rpx;
			height: 70rpx;
			border-radius: 8rpx;
			.item-left {
				// display: flex;
				// flex-direction: column;
				// justify-content: space-between;	
				// .item-adress {
				// 	text {
				// 		font-size: 32rpx;
				// 	}
				// }	
				.item-user {
					display: flex;
					flex-direction: column;
					justify-content: space-between;	
					border-radius: 10rpx;
					.user-name{
						color: #333;
						font-size: 16px;
					}
					.user-phone {
						color: #999;
						font-size: 14px;
					}
				}
			}
			.item-right {
				display: flex;
				align-items: center;				
				color: $color;
				.right_title{
					font-size: 14px;
					color: #666;
					margin-right: 10rpx;
					width: 430rpx;
					height: 100%;
					overflow: hidden;
					text-align: right;
				}
				image{
					width: 20rpx;
					height: 20rpx;
					transform: rotate(90deg); 
				}
			}						
		}
	}	
</style>
